import React from "react";
import DeletePhoto from "../../../assets/image/Dalete photo.png";
import s from "./ArrayImgAgent.module.css";

const ArrayImgAgent = (props) => {

    let splitString = (str) => { //    
        let result;
        result = str.split('/');
        if (result[0] === 'https:' || result[0] === 'http:') { //    url
            result = result[result.length - 1].split('?');
            result = result[0];
        } else {
            result = result[result.length - 1];
            result = result.split('.');
            result = `${result[0]}.${result[result.length - 1]}`;
        }
        return result;
    }

    let changeDataStile = (date) => { //   : "24  2022"
        let year = date.substr(0, 4);
        let month = date.substr(5, 2);
        let day = date.substr(8, 2);

        switch (month) {
            case '01': month = ""; break;
            case '02': month = ""; break;
            case '03': month = ""; break;
            case '04': month = ""; break;
            case '05': month = ""; break;
            case '06': month = ""; break;
            case '07': month = ""; break;
            case '08': month = ""; break;
            case '09': month = ""; break;
            case '10': month = ""; break;
            case '11': month = ""; break;
            case '12': month = ""; break;
        }

        return `${day} ${month} ${year}`;
    }


    let arrowImg; //      
    if (props.idAgent == null) { // id   ,        
        arrowImg = props.fotoForPage.map(el => {
            return (
                <div key={el.id}>
                    <img src={el.img} />
                    <div>
                        {splitString(el.img)}
                    </div>
                    <div>
                        {changeDataStile(el.date)}
                    </div>
                    {/*      */}
                    <div onClick={() => { props.deleteImgForCreate(el.id) }}>
                        <img src={DeletePhoto} alt="del" />
                    </div>
                </div>
            )
        })
    } else {
        arrowImg = props.fotoForPage[props.idAgent].map(el => {
            return (
                <div key={el.id}>
                    <img src={el.img} />
                    <div>
                        {splitString(el.img)}
                    </div>
                    <div>
                        {changeDataStile(el.date)}
                    </div>
                    {/*   */}
                    {props.contactID == props.UserId
                        ? <div onClick={() => { props.deleteImgFromPage(props.idAgent, el.id) }}>
                            <img src={DeletePhoto} alt="del" />
                        </div>
                        : null}
                </div>
            )
        })
    }
    return (
        <div>
            <div className={s.photo_box}>
                {arrowImg}
            </div>
        </div>
    );
}

export default ArrayImgAgent;